.App {
  background-color: #0a3234;

  display: grid;
  grid-template-areas:
  "h h"
  "n c";

  grid-template-rows: 1fr;
  grid-template-columns: 2fr 10fr;

  width: 1200px;
  margin: 50px auto;
  overflow: hidden;

  color: white;
}

.App-content {
  grid-area: c;
  background-color: #403a46;
}